.image-container {
  margin-top: 30px;
  position: relative;
  width: 250px; /* Set the desired width of the image */
  height: 200px; /* Set the desired height of the image */
}

.image-container img {
  width: 100%;
  height: 100%;
  object-fit: cover; /* Optional, to ensure the image fits within the container */
}

.image-container1 {
  margin-top: 0px;
  position: relative;
  width: 250px; /* Set the desired width of the image */
  height: 250px; /* Set the desired height of the image */
}

.image-container1 img {
  width: 100%;
  height: 100%;
  object-fit: cover; /* Optional, to ensure the image fits within the container */
}
.text-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  /* background-color: rgba(0, 0, 0, 0.5); Optional, add a semi-transparent background for better contrast */
  color: black; /* Optional, set the text color */
  padding: 10px; /* Optional, add padding for better readability */
}



